.wrapper {
    position: relative;
    padding: 10px;
    font-size: 20px;
}




.block {
    display: flex;
    align-items: start;
    gap: 20px;
    margin:  0 0 50px 0;

}

.leftSide {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.userPhoto {
    width: 120px;
    height: 120px;
    border-radius: 50%;
}



.blockInfo {
    display: flex;
    border: 1px solid black;
    border-radius: 13px;
    /*flex: 0 1 500px;*/
    width: 400px;
    justify-content: space-between;
    padding: 5px;

}

.about {
    margin:  0 30px 0 0;
}

.place {

}

.btn {
    font-size: 16px;
    background-color: #61dafb;
}

